import React, {memo, useEffect} from 'react';
import Header from "../../partials/Header";
import SideBar from "../Partials/SideBar";
import 'react-perfect-scrollbar/dist/css/styles.css';
import PerfectScrollbar from 'react-perfect-scrollbar'
import {NavLink} from "react-router-dom";
import $ from 'jquery';
import {useTranslation} from "react-i18next";
import Nouislider from "nouislider-react";
import "nouislider/distribute/nouislider.css";
import wNumb from 'wnumb'
import M from 'materialize-css'

export default memo(() => {

    const {t, i18n} = useTranslation();
    useEffect(() => {

        // $('.input-range').on('input', function() {
        //     $(this).next('.range-value').html(this.value);
        // });

        var elems = document.querySelectorAll('select');
        M.FormSelect.init(elems);

        $("ul.list_custom li i").click(function () {
            $(this).toggleClass("active");
            var childs = $(this).closest("li.valed").find("ul#children");
            if ($(this).hasClass("active")) {
                childs.slideDown();
            } else {
                childs.slideUp();
            }

        })


        {/*
         -- range grade
         -- entekhab grade baraye doctor
        */
        }


        //    end range grade


    })


    {/*

        -- language
        -- baraye modiriat do zabaneh kardan site
    */
    }

    function handleLang(e) {
        e.preventDefault();
        if (!localStorage.getItem("lan")) {
            localStorage.setItem("lan", 'en');
        }

        var lan = localStorage.getItem("lan");
        var ckeckbox = $("input[name=cahngelan]");
        if (ckeckbox.is(":checked") && lan === 'en') {
            localStorage.setItem("lan", 'ch');
        } else {
            localStorage.setItem("lan", 'en');
        }

        let lang = localStorage.getItem('lan');
        i18n.changeLanguage(lang);
    }

    //   end language


    return (
        <div>
            <div id="loader-wrapper">
                <div id="loader"></div>
                <div className="loader-section section-left"></div>
                <div className="loader-section section-right"></div>
            </div>

            <Header/>
            <div id="main">
                <div className="wrapper">


                    <SideBar/>

                    <div id="back_shado"></div>
                    <div className="container">
                        <div className="row">

                            <div className="col s12">
                                <div id="breadcrumbs-wrapper">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col s10 m6 l6">
                                                <h5 className="breadcrumbs-title">Doctors list</h5>
                                                <ol className="breadcrumbs">
                                                    <li><NavLink to="/admin">
                                                        <a>Dashboard</a>
                                                    </NavLink></li>
                                                    <li><a className="active">Doctors list</a></li>
                                                </ol>
                                            </div>

                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="col s12" style={{marginBottom: 30}}>

                                <section id="content">

                                    <div id="work-collections">
                                        <div className="row">


                                            <div className="col s12 m12 l6">
                                                <ul id="projects-collection" className="collection z-depth-1">
                                                    <li className="collection-item avatar">
                                                        <i className="material-icons" id="custom_icon">style</i>
                                                        <h6 className="collection-header m-0">Grade(s)</h6>
                                                        <p>Add new grade</p>
                                                    </li>

                                                    <div id={"div-grade-add"}>

                                                        <div className="input-field col s12"
                                                             style={{marginTop: 0, position: "relative"}}>
                                                            <p style={{color: '#ccc'}}>&nbsp;&nbsp;Doctor Name</p>
                                                            <div id="input_back">
                                                                <input placeholder="Doctor name ..."
                                                                       autoComplete="false"
                                                                       name="DoctorName"
                                                                    // onChange={e => inputRegisterChange(e)}
                                                                       type="text" className="validate"/>
                                                            </div>
                                                        </div>

                                                        <div className={"col s12"} style={{marginTop : 20}}>
                                                            <a className={"btn waves-effect blue darken-4"} style={{textTransform : 'unset'}}>Save</a>
                                                        </div>

                                                    </div>



                                                </ul>
                                            </div>

                                            <div className="col s12 m12 l6">
                                                <ul id="projects-collection" className="collection z-depth-1">
                                                    <li className="collection-item avatar">
                                                        <i className="material-icons" id="custom_icon">style</i>
                                                        <h6 className="collection-header m-0">Grade(s)</h6>
                                                        <p>grade's list</p>
                                                    </li>

                                                    <div id={"div-grade-add"}>

                                                        <ul id={"grade-list"}>
                                                            <li>Grade 1</li>
                                                            <li>
                                                                <i className={"material-icons"}>delete</i>
                                                                <i className={"material-icons"}>edit</i>
                                                            </li>
                                                        </ul>

                                                    </div>



                                                </ul>
                                            </div>


                                        </div>
                                    </div>
                                </section>

                            </div>


                        </div>
                    </div>
                </div>
            </div>

        </div>
    );
})